export default {
	data() {
		return {
			goodsList:[],
			advisory:[
				
			]
		}
	},
	onLoad:function(options) {
		uni.request({
			url: 'http://localhost:8080/commodity/selectCom?ccategoryid=' + '',
			method: 'POST',
			success: res => {
				this.goodsList = res.data.message;
				console.log(res.data.message)
			}
		});
		uni.request({
			url: 'http://localhost:8080/listArticleApplet.do',
			method: 'POST',
			data: {},
			success: res => {
				this.advisory = res.data;
				console.log(res.data+"666")
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		HWXQ:function(e){
			uni.showToast({
				title: "编号" + e,
				icon: "none"
			});
			uni.navigateTo({
				url:"../AdvisoryDetails/AdvisoryDetails?id="+e
			})
		},
		XQ:function(e){
			uni.showToast({
				title: "编号" + e,
				icon: "none"
			});
			uni.navigateTo({
				url:"../../producttype/ProductDetails/ProductDetails?id="+e
			})
		}
	}
}
